 import Pickes from '../../components/Picke/Picke.vue'
	export default {
	components: {
			Pickes,
		},
	data() {
		return {
			htmlData:'<view @click="showToast"></view>',
			jsData:`export default {methods: {
				  showToast() { //显示头部
				   this.$refs.Picke.show({message:"显示在头部",position:"top"});
				}
			  }
			}`,
			tabList:[{
				mode:"provinces",
				name:"省",
				value:[10,0,5]
			},{
				mode:"city",
				name:"省市",
				value:[10,0,5]
			},
			{
				mode:"areas",
				name:"省市区",
				value:[10,0,5]
			}],
			tabIndex:0
		}
	},
	computed:{
		mode(){
			return this.tabList[this.tabIndex].mode
		},
		defaultVal(){
			return this.tabList[this.tabIndex].value
		}
	},
	onLoad(e) {
		
	},
	created() {
    
    },
	methods: {
		toggleTab(index){
			this.tabIndex=index;
			this.$refs.Picke.show();
		},
		onConfirm(val){
			console.log(val);
		}
}
}

